import React from "react";
import "./support.css";

const Support = () => {
    return (
        <div className="support container-fluid">
            <h3>Real Support. Real People.</h3>
            <p>We know what it’s like to need support. Our philosophy is to treat you as we would like to be treated. With a hands on support center, online documentation and HD video tutorials, it’s no wonder that some of our best reviews are about our amazing support.</p>
            <div className="row">
                <div className="col-6">
                    <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/support_review.png" alt="text"/>
                </div>
                <div className="col-6">
                    <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/ipads.jpg" alt="img"/>
                    <p>We Are Proudly Serving Over</p>
                    <p className="highlighted-text">400,000</p>
                    <p>Satisfied Customers</p>
                    <p>
                        <a href="#support">read more reviews !</a>
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Support;