import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCog } from "@fortawesome/free-solid-svg-icons";
import "./sidebar.css";

const SideBar = (props) => {
    const [isExpand, setExpand] = useState(false);
    const expand = () => {
        if (isExpand === true) {
            setExpand(false);
        }
        else {
            setExpand(true);
        }
    }
    const status = isExpand ? "expanded" : "not-expanded"
    return (
        <div className="side-bar-container">
            <div className="side-bar-toggle" onClick={expand}>
                <a className="btn-toggle" href="#style" >
                    <FontAwesomeIcon icon={faCog} />
                </a>
            </div>
            <div className={"side-bar-content " + status}>
                <div className="style-selector-wrapper">
                    <div className="ss-content ss-content-first clearfix">
                        <a href="https://themeforest.net/item/avada-responsive-multipurpose-theme/2833226?ref=ThemeFusion" className="fusion-button button-default buy-button ss-button">Buy Avada Now!</a>
                    </div>
                    <div className="ss-content ss-policies">
                        <a href="https://theme-fusion.com/privacy-policy/" >Privacy Policy</a><span className="ss-sep">|</span><a href="https://theme-fusion.com/terms-of-service/" >Terms of Service</a>
                    </div>
                    <span className="ss-title">Layout</span>
                    <div className="ss-content clearfix">
                        <a href="#link" className="wide-button ss-button ss-layout-button active">Wide</a>
                        <a href="#link" className="boxed-button ss-button ss-layout-button">Boxed</a>
                    </div>
                    <span className="ss-title">Boxed Mode Backgrounds</span>
                    <div className="ss-content clearfix">
                        <div className="images patterns">
                            <a href="#link" className="bkgd bkgd1 active" data-name="bkgd1">1</a>
                            <a href="#link" className="bkgd bkgd2" data-name="bkgd2">2</a>
                            <a href="#link" className="bkgd bkgd3" data-name="bkgd3">3</a>
                            <a href="#link" className="bkgd bkgd4" data-name="bkgd4">4</a>
                            <a href="#link" className="pattern1" data-name="pattern1">1</a>
                            <a href="#link" className="pattern2" data-name="pattern2">2</a>
                            <a href="#link" className="pattern3" data-name="pattern3">3</a>
                            <div className="ss-bg-alert">Please select boxed mode to display backgrounds.</div>
                        </div>
                    </div>
                    <div className="ss-content ss-no-styles">
                        <span className="ss-title">Created With Avada</span>
                        <span className="ss-desc">Avada's powerful setup allows you to easily create unique looking sites. All demos can be installed with one click.</span>
                    </div>
                    <div className="ss-content demos-wrapper clearfix">
                        <div className="demos">
                            <div className="demo-sites clearfix row">
                                {props.listImgs.map((item, index) => {
                                    return (
                                        <div className="col-6 thumb" key={index}>
                                            <img src={item.img} alt={item.name} />
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SideBar;