import React from "react";
import "./banner.css";

const Banner = () => {
    return (
        <div className="banner row">
            <div className="col-12 intro">
                <h1>#1 Selling Theme of All Time</h1>
            </div>
            <div className="col-12 point">
                <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/not_required1.png" width="521" height="60" alt="Coding Not Required" />
            </div>
            <div className="col-12 image">
                <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/avada_build_unique.png" alt="i" />
            </div>
        </div>
    )
}

export default Banner;