import React from "react";
import Banner from "./banner";
import About from "./about";
import More from "./more";
import GreenDiamond from "./diamonds/GreenDiamond";
import WhiteDiamond from "./diamonds/WhiteDiamond";
import BlueDiamond from "./diamonds/BlueDiamond";
import PurpleDiamond from "./diamonds/PurpleDiamond";
import GrayDiamond from "./diamonds/GrayDiamond";
import Thumbnail from "./thumbnail";
import ChangeContent from "./change-content";
import Detail from "./detail";
import Buy from "./buy";
import Offer from "./offer";
import Option from "./option";
import Additional from "./additional";
import Support from "./support";
import Extension from "./extension";
import ReviewHeader from "./review/ReviewHeader";
import ReviewContent from "./review/ReviewContent";
import FusionBuilder from "./FusionBuilder/FusionBuilder";
import FusionBuilderContent from "./FusionBuilder/FusionBuilderContent";
import "./main.css";
import TrendingHeader from "./Trending/TrendingHeader";
import TrendingContent from "./Trending/TrendingContent";
import IntegrationHeader from "./Integration/IntegrationHeader";
import IntegrationContent from "./Integration/IntegrationContent";
import FlexibleHeader from "./Flexible/FlexibleHeader";
import FlexibleContent from "./Flexible/FlexibleContent";
import Final from "./final";

const Main = (props) => {
    return (
        <div className="main">
            <Banner />
            <About />
            <More />
            <GreenDiamond />
            <Thumbnail listImgs={props.listImgs} />
            <ChangeContent />
            <GreenDiamond />
            <Detail />
            <Buy />
            <Offer />
            <WhiteDiamond />
            <Option />
            <GreenDiamond />
            <Additional />
            <Support />
            <Extension />
            <ReviewHeader />
            <WhiteDiamond />
            <ReviewContent />
            <FusionBuilder />
            <GreenDiamond />
            <FusionBuilderContent />
            <TrendingHeader />
            <BlueDiamond />
            <TrendingContent />
            <IntegrationHeader />
            <PurpleDiamond />
            <IntegrationContent />
            <FlexibleHeader />
            <GrayDiamond />
            <FlexibleContent />
            <Final />
        </div>
    )
}

export default Main