                        <a href="https://avada.theme-fusion.com/portfolio-items/className-et-aptent/" target="_self" rel="" title="className Et Aptent">
                            <img width="66" height="66" src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port11-66x66.jpg" className="attachment-recent-works-thumbnail size-recent-works-thumbnail wp-post-image" alt="" srcSet="https://avada.theme-fusion.com/wp-content/uploads/2014/09/port11-66x66.jpg 66w, https://avada.theme-fusion.com/wp-content/uploads/2014/09/port11-150x150.jpg 150w" sizes="(max-width: 66px) 100vw, 66px" /> </a>
